body
{
	background: #10121b;
	font-family: 'Nunito', sans-serif !important;
}

nav
{
	width: 260px;
	height: 100vh;
	left: 0;
	transition: 0;
	background: #191d2b;
	border-right: 1px solid #2e344e;
	position: fixed;
	transition: all .4s ease-out;
	z-index: 2;
	font-family: 'Nunito', sans-serif;
}

button:focus
{
	outline: none;
}

.nav_inner
{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	overflow: hidden;
	overflow-y: auto;
}

.nav_my_image
{
	padding: 20px 0;
	display: block;
	width: 100%;
	text-align: center;
	border-bottom: 1px solid #2e344e;
}

.nav_my_image a
{
	height: 150px;
	width: 150px;
	border-radius: 1000px;
	border: 4px solid #2e344e;
	overflow: hidden;
	display: inline-block;
	vertical-align: middle;
}

.nav_my_image a:hover, .nav_my_image a:active
{
	cursor: url(data:image/png;base64,), auto;
}

.nav_image
{
	max-width: 100%;
}

.nav_menu
{
	width: 100%;
	padding: 25px 5px;
	color: white;
}

.nav_menu ul
{
	margin-bottom: 1rem;
	padding: 0px;
}

.nav_menu li
{
	list-style: none;
	text-align: center;
	margin-top: 1.5px;
	display: block;
}

.nav_menu li a
{
	text-transform: uppercase;
	font-size: .94rem;
	letter-spacing: 1px;
	display: block;
	padding: 5px 0;
	color: #a4acc4;
	font-weight: 600;
	position: relative;
	overflow: hidden;
	z-index: 1;
	text-decoration: none;
}

.nav_menu li a:before
{
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	right: 100%;
	height: 100%;
	width: 0;
	background: rgba(3,127,255,.2);
	visibility: hidden;
	z-index: -1;
	transition: all .4s ease-out;
}

.nav_menu li a:hover
{
	cursor: url(data:image/png;base64,), auto;
}

.nav_menu li a:hover::before
{
	width: 100%;
	visibility: visible;
}

.nav_menu li a.active
{
	color: #fff;
	font-weight: 800;
}

.nav_menu li a.active::before
{
	background: #037fff;
	width: 100%;
	visibility: visible;
}

.nav_copyright
{
	display: block;
	width: 100%;
	text-align: center;
	margin-bottom: 0;
	/* font-size: 1rem; */
	font-size: 10px;
	padding: 15px 0;
	border-top: 1px solid #2e344e;
	color: #fff;
}

/*Responsive Menu*/
.responsive_toggel
{
	position: absolute;
	left: 100%;
	top: 20px;
	padding: 0;
	height: 50px;
	width: 50px;
	text-align: center;
	font-size: 1.6rem;
	background: #191d2b;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;

	border: 1px solid #2e344e;
	font-family: Nunito,sans-serif;
	color: #a4acc4;

	cursor: url(data:image/png;base64,), auto;
}

.responsive_toggel .cross
{
	display: none;
}
