/*About Content*/
.about-content
{
	padding-top: 120px;
}

.about_title
{
	margin-bottom: 60px;
	font-family: Nunito,sans-serif;
	position: relative;
}

.about_title h2
{
	font-weight: 700;
	position: relative;
	padding-bottom: 15px;
	text-transform: uppercase;
	color: #fff;
	padding-bottom: 15px;
	font-size: 2.57rem;
	line-height: 3.2857rem;
	position: relative;
}

.about_title h2:before, .about_title h2:after
{
	content: "";
	position: absolute;
	left: 0;
	top: auto;
	bottom: 0;
	height: 5px;
	border-radius: 100px;
}

.about_title h2:before
{
	width: 100px;
	background: rgba(3,127,255,.3);
}

.about_title h2:after
{
	width: 35px;
	background: #037fff;
}

.about_title span
{
	position: absolute;
	left: 0;
	top: 100%;
	font-size: 6rem;
	line-height: 1;
	font-weight: 900;
	color: rgba(25,29,43,.44);
	display: inline-block;
	text-transform: uppercase;
	z-index: -1;
	transform: translateY(-40%);
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	white-space: nowrap;
	font-size: 4.5rem;
}

.about_image_div
{
	position: relative;
}

.about_image_div::before, .about_image_div::after
{
	content: "";
	position: absolute;
	height: 65%;
	width: 15px;
	background: rgba(3,127,255,.6);
}

.about_image_div::before
{
	left: 0;
	top: 0;
}

.about_image_div::after
{
	left: auto;
	right: 0;
	top: auto;
	bottom: 0;
}

.about_image_div img
{
	width: 100%;
}

